:root {
    --dark: rgb(40, 40, 40);
    --light: rgb(151, 151, 151);
    --green: #26401F;
    --green-2: #416a36;
    --green-light: #AFBF75;
    --purple: #8C3575;
}

* {
    margin: 0px;
    box-sizing: border-box;
    font-family: "ADega";
}

@font-face {
    font-family: "ADega";
    src: url("./fonts/ADega Serif Regular.otf");
}
@font-face {
    font-family: "ADega Bold";
    src: url("./fonts/ADega Serif Bold.otf");
}

@font-face {
    font-family: "Belly Betty";
    src: url("./fonts/Belly\ Betty.ttf");
}


h1, h2, h3, h4, h5, h6 {
    font-family: "ADega Bold";
    /* font-family: "Belly Betty"; */

}

body {
    background: radial-gradient(#d9ec95, #aebe75);

}

.App {
    position: relative;
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}


canvas {
    /* background-color: #731d11; */
    /* filter: blur(8px); */
    /* background-color: var(--green-light); */
    z-index: 0;
    transition-property: transform;
    transition-duration: 1.5s;
}


.Title-Cont {
    position: absolute;
    min-width: 100vw;
    min-height: 97vh;
    margin: auto;
    top: 0px;
    display: flex;
    justify-content: space-between;
    padding: 5vh 32px 5vh 32px;
    align-items: flex-start;
    flex-direction: column;

}

.Title {
    border: 2px solid var(--green);
    padding: 12px 18px;
    /* background-color: white; */
}

.Title h1 {
    font-size: 42px;
    color: var(--green);
}

.Title h3 {
    font-size: 16px;
    width: 100%;
    text-align: end;
    color: var(--green)
}

.Bottom-Bar {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.Title-Button-Cont {
    display: flex;
    flex-direction: column;
    min-height: 100%;
}

.Title-Button {
    /* height: 100%; */
    padding: 12px;
    margin: 6px 0px;
    border-radius: 12px;
    /* width: 200px; */
    width: 20vw;
    max-width: 200px;
    color: white;
    transition: all 0.1s;
}

.Title-Button:hover {
    filter: brightness(1.4);
}

.Start-Button {
    background-color: var(--purple);
    flex-grow: 0;
    height: 100%;
    font-size: 32px;
    display: flex;
    justify-content: space-between;
}

.Info-Button {
    background-color: var(--green);
}

.Subtitle {
    /* font-size: 32px; */
    color: var(--green);
    align-self: flex-start;
    margin-right: 24px;
}

.Subtitle h1 {
    font-weight: 900;
    font-family: "ADega Bold";
    font-size: 96px;
}
.Subtitle h2 {
    font-weight: 900;
    font-family: "ADega";
    font-size: 48px;
}

.Mini-Title {
    position: absolute;
    top: 0px;
    width: 100vw;
    /* display: flex; */
    justify-content: center;
    padding: 12px;
    font-size: 32px;
    color: var(--green-2);

}

.Mini-Title > h5 {
    border: 1px solid var(--green-2);
    padding: 4px 8px;
}

.Fade-Object-Active {
    display: flex;
    animation-name: fadein;
    animation-duration: 1s;
}

@keyframes fadein {
    0% {
        opacity: 0%
    }

    25% {
        opacity: 0%
    }

    100% {
        opacity: 100%;
    }
}

.Fade-Object-Inactive {
    display: none;
    animation-name: fadeout;
    animation-duration: 0.8s;
}

@keyframes fadeout {
    0% {
        opacity: 100%;
        display: flex;
    }
    

    50% {
        opacity: 0%;
        display: flex;
    }

    100% {
        opacity: 0%;
        display: none;
    }
}

.Overlay {
    position: absolute;
    /* display: flex; */
    justify-content: center;
    align-items: flex-end;
    width: 40%;
    min-width: 300px;
    min-height: 100vh;
    padding: 20px;
    pointer-events: none;
}

.Overlay-Back {
    pointer-events: all;
    cursor: pointer;
    text-decoration: underline;
    text-underline-offset: 4px;
    text-decoration-color: #666;
    color: #666;
    margin-bottom: 12px;
    display: inline-block;
    transition: all 0.2s;
}

.Overlay-Back:hover {
    transform: translateX(-4px);

}

.Text {
    /* display: none; */
    transition: all 0.5s;
    transition-timing-function: ease-in-out; 
    position: absolute;
    padding: 36px;
    margin-bottom: 64px;
}

 
.Text-Active {
    /* display: block; */
    opacity: 100%;  
    transition-delay: 0.3s;
    animation-name: slidein;
    animation-duration: 0.5s;
    animation-delay: 0.3s;
}

@keyframes slidein {
    from {
        transform: translateX(-80px);
    }

    to {
        transform: translateX(0px);
    }
}

.Text-Inactive {
    /* display: none; */
    opacity: 0;
    animation-name: slideout;
    animation-duration: 0.5s;
    animation-delay: 0s;
    transition-delay: 0s;
}

@keyframes slideout {
    from {
        transform: translateX(0px);
    }

    to {
        transform: translateX(80px);
    }
}


.Text > h1 {
    margin-bottom: 4px;
}

.Author {
    text-transform: uppercase;
    margin-bottom: 16px;
    font-size: 14px;
}




.Selector {
    padding: 12px;
    width: 100%;
    position: absolute;
    bottom: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Selector > div > div {
    padding: 4px 4px;
    /* background-color: red; */
}

.Selector div > span {
    border: none;
    background-color: rgba(255,255,255,0.1);
    outline: 2px solid var(--light);
    color: var(--light);
    padding: 6px;
    border-radius: 256px;
    transition: all 0.2s;
    font-size: 0px;
}

.Selector > div:hover {
    padding: 0px 8px;
    margin: 0px -4px;
}

.Selector > div:hover > div > span{
    padding: 8px 8px;
    font-size: 14px;
    /* margin: 2px 4px; */
    outline-color: var(--dark);
    color: var(--dark);
}

.Selector > div:hover > .ActiveButton > span > span {
    padding: 0px;
}


.Selector .ActiveButton > span {
    padding: 10px 8px;
    font-size: 0px;
    /* margin: 0 4px; */
    outline: 2px solid var(--green);
    color: var(--dark);
} 

.Selector .ActiveButton > span > span {
    padding: 3px;
    background-color: var(--green);
    border-radius: 50px;
}

.Loading {
    position: absolute;
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.Load-Bar {
    width: 120px;
    padding: 4px;
    border: 1.5px solid var(--green);
    margin-top: 12px;
}

.Load-Bar > div {
    padding: 4px;
    background-color: var(--purple);
}


.Toc-Rows {
    position: absolute;
    top: 0px;
    /* display: flex; */
    flex-direction: column;
    justify-content: center;
    width: 25vw;
    height: 100%;
    padding: 28px;
    opacity: 65%;
}

.Toc-Rows > div {
    margin-bottom: max(2.5vh, 8px);
    padding-bottom: 8px;
    padding-left: 4px;
    border-bottom: 1px solid rgba(0,0,0,0.2);
    width: 100%;
    max-width: 250px;
    transition: all 0.2s;
    background-color: rgba(0,0,0,0);
    border-left: 0px solid var(--purple);
    position: relative;
}



.Toc-Rows > div > h4 {
    font-size: 24px;
    font-family: "ADega";
    margin: 4px 0;
}

.Toc-Rows > div > span {
    font-size: 12px;
}

.Toc-Left {
    left: 0px;
    align-items: flex-start;
}

.Toc-Left > div:hover {
    padding-left: 12px;
    color: var(--purple);
}

.Toc-Right {
    right: 0px;
    align-items: flex-end;
}

.Toc-Right > div:hover {
    padding-right: 12px;
    color: var(--purple);
}

.Toc-Right * {
    text-align: right;
}


.Hover-Line-Cont {
    position: absolute;
    z-index: 10;
    top: 0px;
    left: 0px;
    width: 100vw;
    height: 100vh;
    pointer-events: none;
    opacity: 20%;
}

.Line-Target {
    padding: 2px;
    display: block;
    position: absolute;
    top: 50%;
    
}

.Toc-Left .Line-Target {
    right: 0px;
}

.Toc-Right .Line-Target {
    left: 0px;
}

line {
    stroke: linear-gradient(red, blue);
}



.Info {
    position: absolute;
    top: 0px;
    left: 0px;
    height: 100vh;
    width: 100vw;
    padding: 48px 24px 0 24px;
    flex-direction: column;
}

.Info .Back {
    margin-bottom: 24px;
    font-size: 24px;
    font-weight: 900;
    cursor: pointer;
}

.Info .Back:hover {
    opacity: 60%;
}

.Info > div {
    width: 35vw;
    overflow: scroll;
}

.Info-Title h1 {
    color: var(--green);
}

.Info-Title h2 {
    opacity: 65%;
    margin-bottom: 8px;
}

.Info-Section {
    margin: 24px 0;
}

.Info-Section h2 {
    margin-bottom: 10px;
    padding-bottom: 2px;
    border-bottom: 1px solid rgba(0,0,0,0.4);
}


.ReadMore {
    position: absolute;
    bottom: 0px;
    width: 100vw;
    padding: 12px;

    justify-content: center;
}

.ReadMore div {
    padding: 12px;
    background-color: #8C3575;
    color: white;
    font-size: 18px;
    border-radius: 12px;
    cursor: pointer;
    width: 30vw;
    text-align: center;
}


@media screen and (max-width: 800px) {
    .Overlay {
        bottom: 30px;
        width: 100%;
        height: 30vh;
        min-height: 30vh;
    }

    .Text {
        bottom: 0px;
        margin-bottom: 28px;
    }
}